.conference-slide {
  background-color: #fff;
  border-radius: 5px;
  box-shadow: 1px 0px 5px grey;
  width: 400px;
  cursor: pointer;
  margin: 50px;
}
.conference-slide:hover {
  transform: scale(1.1);
}
.conference-slide:hover .slide-footer {
  display: flex;
}
.conference-slide .slide-image img {
  width: 100%;
  height: 200px;
  border-top-right-radius: 5px;
  border-top-left-radius: 5px;
}
.conference-slide .slide-title {
  font-size: 1.2rem;
  line-height: 1.5rem;
  padding-bottom: 10px;
}
.conference-slide .slide-date,
.conference-slide .slide-type,
.conference-slide .slide-location {
  color: gray;
  font-size: 1em;
  font-weight: 800;
  padding-bottom: 10px;
}
.conference-slide .slide-type {
  display: flex;
  align-items: center;
}
.conference-slide .slide-type div {
  display: flex;
  align-items: center;
  gap: 5px;
}
.conference-slide .slide-type:before {
  content: url("../../images/bookmark.png");
  background-image: url("../../images/bookmark.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: auto;
  width: 16px;
  height: 16px;
  display: block;
  margin: 0 6px 0 0;
}
.conference-slide .slide-date {
  display: flex;
  align-items: center;
}
.conference-slide .slide-date:before {
  content: " ";
  background-image: url("../../images/calendar-date.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: auto;
  width: 16px;
  height: 16px;
  display: block;
  margin: 0 6px 0 0;
}
.conference-slide .slide-location {
  display: flex;
  align-items: center;
}
.conference-slide .slide-location:before {
  content: "";
  background-image: url("../../images/placeholder.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: auto;
  width: 16px;
  height: 16px;
  display: block;
  margin: 0 6px 0 0;
}
.conference-slide .slide-body {
  padding: 20px 30px;
}
.conference-slide .slide-footer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-top: 1px solid grey;
  display: none;
}

.slide-favourite,
.slide-download,
.slide-share {
  padding: 10px;
  border-right: 1px solid grey;
  text-align: center;
  cursor: pointer;
}
.slide-favourite img,
.slide-download img,
.slide-share img {
  height: 24px;
  width: 24px;
}

.slide-share-details {
  color: blue;
  font-size: 1rem;
  font-weight: 600;
  padding: 10px;
  text-align: center;
}
.slide-share-details a {
  text-decoration: none;
}

.slide-favourite {
  width: 20%;
}

.slide-download {
  width: 20%;
}

.slide-share {
  width: 20%;
}

.slide-share-details {
  width: 40%;
}